<template>
  <div>
    <Header title="" />
    <div class="container pt-5">
      <h5></h5>
      <hr />
      <TeacherCard :teacher="th17" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th15" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th14" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th13" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th10" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th03" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th02" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th01" />

      <h5></h5>
      <hr />
      <TeacherCard :teacher="th" />
    </div>
  </div>
</template>
<script>
import axios from "axios";
import _ from "lodash";
import Header from "@/components/Header";
import TeacherCard from "./components/TeacherCard";
export default {
  components: { Header, TeacherCard },
  data() {
    return {
      teacherKey: 0,
      th01: [],
      th02: [],
      th03: [],
      th10: [],
      th13: [],
      th14: [],
      th15: [],
      th17: [],
      th: [
        {
          name: "",
          specail: "00",
          emp_email: "phtsai@fcu.edu.tw",
          skill: [
            {
              ski_desc_c:
                "",
            },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "//" },
            {
              ski_desc_c:
                " ",
            },
            {
              ski_desc_c:
                "",
            },
            {
              ski_desc_c:
                "",
            },
            { ski_desc_c: "" },
            {
              ski_desc_c: "",
            },
          ],
          emp_ext: "#5102",
        },
        {
          name: "",
          specail: "00",
          emp_email: "yhliao@mail.fcu.edu.tw",
          skill: [
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
            { ski_desc_c: "" },
          ],
          emp_ext: "#5103",
        },
      ],
    };
  },
  mounted() {
    this.getTeacher();
  },
  methods: {
    getTeacher() {
      axios
        .get("/fcu_api/Teacher.json")
        .then((res) => {
          var data = res.data;
          var initData = _.groupBy(data, "specail");
          this.th01 = _.orderBy(initData["01"], ["name"], ["asc"]);
          this.th02 = _.orderBy(initData["02"], ["name"], ["asc"]);
          this.th03 = _.orderBy(initData["03"], ["name"], ["asc"]);
          this.th10 = _.orderBy(initData["10"], ["name"], ["asc"]);
          this.th13 = _.orderBy(initData["13"], ["name"], ["asc"]);
          this.th14 = _.orderBy(initData["14"], ["name"], ["asc"]);
          this.th15 = _.orderBy(initData["15"], ["name"], ["asc"]);
          this.th17 = _.orderBy(initData["17"], ["name"], ["asc"]);
        })
        .catch((err) => {
          console.log(err.response);
        });
    },
    eventEmail(email, type) {
      if (!email) return;
      if (type) {
        return email.split("@")[0];
      }
      return email.split("@")[1];
    },
  },
};
</script>

