<template>
  <div>
    <Header title="" />
    <div class="container">
      <el-card :body-style="{ padding: '0px' }">
        <div style="padding: 16px 16px 0px 16px">
          <h5></h5>
          <p class="mb-0">
            21 IDC
            
          </p>
        </div>
        <img
          src="http://www.math.fcu.edu.tw/2021_new_website/data/img/1577067361369.jpg"
          style="width:100%"
        />
      </el-card>
      <TableFile :tableData="courseData" :key="courseKey" />
    </div>
  </div>
</template>
<script>
import axios from "axios";
import Header from "@/components/Header";
import TableFile from "@/components/TableFile";
export default {
  components: { Header, TableFile },
  data() {
    return {
      activeName: "1",
      courseKey: 0,
      courseData: [],
    };
  },
  mounted() {
    this.getCourse();
  },
  methods: {
    getCourse() {
      axios
        .get("/2021_new_website/data/course_article.json")
        .then((res) => {
          this.courseData = res.data;
          this.courseKey += 1;
        })
        .catch((err) => {
          console.log(err.response);
        });
    },
  },
};
</script>
